import Vue from 'vue';
import VueRouter from 'vue-router';
import loginRoute from '../modules/login/router';
import encuesta from '../modules/encuesta/router';
import SuperAdmin from '../modules/admin/router';
import votos from '../modules/votos/router'


Vue.use(VueRouter)

const routes = [
  {
    path:"/",
    ...encuesta
  },
  {
    path:'/login',
    ...loginRoute
  },
  {
    path:"/superAdmin",
    ...SuperAdmin
  },
  {
    path:"/votos",
    ...votos
  },
  {
    path: '**',
    redirect: '/'
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
